export default{
	data(){
		return{
			id:"",
			dataObj:{
				timeMoney:"",
				useTimeMoney:1,
				loveValue:0,
				allianceMonetary:"",
				b2cMonetary:"",
				xslbAvailable:0,
				isOnLine:"",
				invitationCode:"",
				invitationUserNumber:"",
				allInvitationUserMonetary:"",
				allInvitationCountTimeBean:"",
				monthlySignInNumber:"",
				signInGetTimeBeanNumber:""
			}
		}
	},
	methods: {
		searchData() {
			let that=this;
			that.$ajax({
				url:"/memberManager/searchMemberDetails",
				data:{
					mid:that.id
				},success(d){
					that.dataObj=d;
					console.log(d)
				}
			})
		}
	},
	onLoad(e){
		this.id=e.id;
		this.searchData()
	}
}
